body {
    margin: 0;
    padding: 0;
    width: 100vw;
    background: #edddd4;
    font-family: 'montserrat';
}

header {
    margin: 0;
    padding: 0;
    height: 38vh;
    top: 0;
    left: 0;
}

.topo {
    background: url("../media/topo-bg.png");
    opacity: 0.4;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 10vh;
    background-size: 100%;
}

h1 {
    font-family: 'lobster', serif;
    color: #c44536;
    font-size: 20vw;
    text-align: center;
    margin: 20px 10px;
    transform: rotate(-10deg);
}

h2 {
    font-family: 'Montserrat', serif;
    color: white;
    font-size: 8vw;
    margin: 0 0 20px 0;
    text-align: center;
    transform: rotate(-10deg);
}

h3 {
    font-family: 'lobster', serif;
    color: #197278;
    font-size: 8vw;
    margin: 0 0 20px 0;
    text-align: center;
    /* transform: rotate(-10deg); */
}

